    text-align: center;
    font-weight: bold;
    line-height: 15px;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.435);
  }
  #allwrap #loginbox .info form #loginbtn {
    width: 360px;
    height: 45px;
    margin: 30px 45px 0;
    background-color: rgba(65, 105, 225, 0.601);
    border: none;
    cursor: pointer;
  }
  #allwrap #loginbox .info form p {
    text-align: center;
    padding: 20px 25px 0;
  }
  #allwrap #loginbox .info form #qrloginbtn {
