  }
  .btmmenu a.banner:hover {
    background-position: 0 0, 0 0, 0 0;
  }
  .btmmenu a.banner img.mainlogo {
    transition: 0.2s;
    position: absolute;
    animation-name: updown;
    animation-duration: 2.5s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out;
  }
  .btmmenu a.banner:hover > img.mainlogo {
    animation-name: up;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    opacity: 0;
  }
  .btmmenu a.banner:hover img.five {
    opacity: 1;
  }
  .btmmenu a.banner img.five {
    transition: 0.3s;
    position: absolute;
    opacity: 0;
    animation-name: shake;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
}
