/* 애니메이션 keyframe 모음 ---------------------- */
@keyframes shake {
  0% {
    rotate: 10deg;
  }
  25% {
    rotate: -10deg;
  }
  50% {
    rotate: 10deg;
  }
  75% {
    rotate: -10deg;
  }
  100% {
    rotate: 10deg;
  }
}
@keyframes up {
  0% {
    top: 0;
  }
  100% {
    top: -200px;
  }
}
@keyframes updown {
  0% {
    top: -8px;
  }
  25% {
    top: 8px;
  }
  50% {
    top: -8px;
  }
  75% {
    top: 8px;
  }
  100% {
    top: -8px;
  }
}
@media (hover: none) {
  #allwrap {
    position: fixed;
    transform: scale(50%); /* 화면 전체를 80% 크기로 축소 */
    left: 50%;
    top: 40%;
    /* 축소의 기준점을 좌측 상단으로 설정 */
  }
