@charset "utf-8";
*{margin: 0; padding: 0;}
li{list-style-type: none;}
a{text-decoration: none; color: inherit;}
.inner{width: 1200px; margin: 0 auto;}
body{font: 12px '돋움',dotum,sans-serif; color: #333; }
