      display: flex; justify-content: space-between;
      line-height: 25px;
      margin: 0 auto; margin-bottom: 10px;
      li{
        select{
          background-color: #ffffff00; border: none;
        }
        
      }
    }
    p.copy{ text-align: center; color: rgba(0, 0, 0, 0.344); padding-bottom: 15px;}

  }
  


}







/* 애니메이션 keyframe 모음 ---------------------- */
@keyframes shake {
  0%{rotate: 10deg;}
  25%{rotate: -10deg;}
  50%{rotate: 10deg}
  75%{rotate: -10deg;}
  100%{rotate: 10deg;}
}

@keyframes up {
  0%{top: 0;}
  // 25%{top: -50px;}
  // 50%{top: -100px;}
  // 75%{top: -150px;}
  100%{top: -200px;}  
}

@keyframes updown{
  0%{top: -8px;}
  25%{top: 8px;}
  50%{top: -8px;}
  75%{top: 8px;}
  100%{top: -8px;}
  
}


@media (hover:none) {
  #allwrap {
    position: fixed;
    transform: scale(50%); /* 화면 전체를 80% 크기로 축소 */
    left: 50%; top: 40%;
    /* 축소의 기준점을 좌측 상단으로 설정 */
  }
  
}
